<template>
    <div>
       {{stor.user.name}}
       <van-button plain type="primary" @click="fg()">一键进入</van-button>
    </div>
  </template>
  
<script>
import { mapState,mapMutations } from 'vuex';
    export default{
        computed:{
            ...mapState(['stor'])
        },
        data(){
            return{
                lps:''
            }
        },
        created(){
            
        },
        methods:{
            ...mapMutations('stor',['setuser']),
            // ...mapMutations('stor',['setuser']),
            fg(){
                let obj={name:'admin',token:true}
                this.setuser(obj)
                sessionStorage.setItem('user',JSON.stringify(obj))
                this.$router.push('/')
            },
        }
    }
</script>

