const modul={
    namespaced:true,
    state: {
        user:{
            name:'',
            token:''
        }
    },
    getters: {
    },
    mutations: {
        setuser(state,pil){
            state.user=pil
        },
        clearuser(state){
            state.user={name:'',token:''}
        }
    },
    actions: {
    },
}
export default modul