import Vue from 'vue'
import VueRouter from 'vue-router'
import buju from '../views/buju.vue'
import Home from '../views/neirong/Home.vue'
// import Search from '../views/neirong/search.vue'
const Search=()=>import('../views/neirong/search.vue')
const People=()=>import('../views/neirong/people.vue')
const Liaot=()=>import('../views/neirong/liaot.vue')
import login from '../views/login/login.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'buju',
    component: buju,
    meta:{
      isLogin:true
    },
    children:[
      {
        path:'/',
        name:'Home',
        component:Home,
      },
      {
        path:'/search',
        name:'Search',
        component:Search,
      },
      {
        path:'/people',
        name:'People',
        component:People,
      },
      {
        path:'/liaot',
        name:'Liaot',
        component:Liaot,
      },
    ]
  },
  {
    path: '/login',
    name: 'login',
    component: login,
  },
  // {
  //   path: '/about',
  //   name: 'about',
  //   // route level code-splitting
  //   // this generates a separate chunk (about.[hash].js) for this route
  //   // which is lazy-loaded when the route is visited.
  //   component: () => import(/* webpackChunkName: "about" */ '../views/AboutView.vue')
  // }
]

const router = new VueRouter({
  routes
})

import store from '../store/index'

router.beforeEach((to,from,next)=>{
  console.log('-----to-----',to);
  //判断是否要登录
  if(to.matched.some(ele=>ele.meta.isLogin)){
    //判断用户是否以及登录
    let token=store.state.stor.user.token;
    if(token){
      next();
    }else{
      next('/login')
    }
  }else{
    next();
  }
  
})

export default router
